         <el-col :span="12">
           <el-form-item v-if="flag=='lingdao'"  label='' prop="lingdaoIdNumber">
               <el-input v-model="ruleForm.lingdaoIdNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
             <el-form-item v-if="flag=='lingdao'" label='' prop="lingdaoPhoto">
                 <file-upload
                         tip=""
                         action="file/upload"
                         :limit="3"
                         :multiple="true"
                         :fileUrls="ruleForm.lingdaoPhoto?$base.url+ruleForm.lingdaoPhoto:''"
