                 ></file-upload>
             </el-form-item>
         </el-col>
         <el-col :span="12">
           <el-form-item v-if="flag=='lingdao'"  label='' prop="lingdaoEmail">
               <el-input v-model="ruleForm.lingdaoEmail"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='renliziyuan'"  label='' prop="renliziyuanName">
               <el-input v-model="ruleForm.renliziyuanName"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='renliziyuan'"  label='' prop="renliziyuanPhone">
               <el-input v-model="ruleForm.renliziyuanPhone"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='renliziyuan'"  label='' prop="renliziyuanIdNumber">
               <el-input v-model="ruleForm.renliziyuanIdNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
             <el-form-item v-if="flag=='renliziyuan'" label='' prop="renliziyuanPhoto">
                 <file-upload
                         tip=""
                         action="file/upload"
                         :limit="3"
                         :multiple="true"
                         :fileUrls="ruleForm.renliziyuanPhoto?$base.url+ruleForm.renliziyuanPhoto:''"
                         @change="renliziyuanPhotoUploadChange"
                 ></file-upload>
             </el-form-item>
         </el-col>
         <el-col :span="12">
           <el-form-item v-if="flag=='renliziyuan'"  label='' prop="renliziyuanEmail">
               <el-input v-model="ruleForm.renliziyuanEmail"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='xingzhengrenyuan'"  label='' prop="xingzhengrenyuanName">
               <el-input v-model="ruleForm.xingzhengrenyuanName"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='xingzhengrenyuan'"  label='' prop="xingzhengrenyuanPhone">
               <el-input v-model="ruleForm.xingzhengrenyuanPhone"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='xingzhengrenyuan'"  label='' prop="xingzhengrenyuanIdNumber">
               <el-input v-model="ruleForm.xingzhengrenyuanIdNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
             <el-form-item v-if="flag=='xingzhengrenyuan'" label='' prop="xingzhengrenyuanPhoto">
                 <file-upload
                         tip=""
                         action="file/upload"
                         :limit="3"
                         :multiple="true"
                         :fileUrls="ruleForm.xingzhengrenyuanPhoto?$base.url+ruleForm.xingzhengrenyuanPhoto:''"
                         @change="xingzhengrenyuanPhotoUploadChange"
                 ></file-upload>
             </el-form-item>
         </el-col>
         <el-col :span="12">
           <el-form-item v-if="flag=='xingzhengrenyuan'"  label='' prop="xingzhengrenyuanEmail">
               <el-input v-model="ruleForm.xingzhengrenyuanEmail"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='yonghu'"  label='' prop="yonghuName">
               <el-input v-model="ruleForm.yonghuName"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='yonghu'"  label='' prop="yonghuPhone">
               <el-input v-model="ruleForm.yonghuPhone"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='yonghu'"  label='' prop="yonghuIdNumber">
               <el-input v-model="ruleForm.yonghuIdNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
             <el-form-item v-if="flag=='yonghu'" label='' prop="yonghuPhoto">
                 <file-upload
                         tip=""
